import React, {useEffect, useRef, useState} from "react";
import {ChartingLibraryWidgetOptions, IChartingLibraryWidget, ResolutionString, widget as Widget} from "@charting-library";
import {datafeed} from "./api/datafeed";

interface ChartProps {
    symbol: string
    height: number
}

export interface ChartContainerProps {
    symbol: ChartingLibraryWidgetOptions['symbol'];
    interval: ChartingLibraryWidgetOptions['interval'];

    libraryPath: ChartingLibraryWidgetOptions['library_path'];
    chartsStorageUrl: ChartingLibraryWidgetOptions['charts_storage_url'];
    chartsStorageApiVersion: ChartingLibraryWidgetOptions['charts_storage_api_version'];
    clientId: ChartingLibraryWidgetOptions['client_id'];
    userId: ChartingLibraryWidgetOptions['user_id'];
    fullscreen: ChartingLibraryWidgetOptions['fullscreen'];
    autosize: ChartingLibraryWidgetOptions['autosize'];
    studiesOverrides: ChartingLibraryWidgetOptions['studies_overrides'];
}

export const Chart = (props: ChartProps) => {
    const [tvWidget, setTvWidget] = useState<IChartingLibraryWidget | null>(null)
    const containerRef = useRef<HTMLDivElement>(null)

    useEffect(() => {
        const viewWidget = new Widget({
            symbol: props.symbol,
            datafeed: datafeed,
            interval: '1D' as ResolutionString,
            library_path: '/charting_library/',

            height: props.height,

            theme: 'Dark',
            locale: 'en',
            disabled_features: ['use_localstorage_for_settings', 'header_symbol_search'],
            charts_storage_url: 'https://saveload.tradingview.com',
            charts_storage_api_version: '1.1',
            client_id: 'tradingview.com',
            user_id: 'public_user_id',
            fullscreen: false,
            autosize: true,
            studies_overrides: {},
            container: containerRef.current as HTMLElement
        })
        setTvWidget(viewWidget)

        return () => {
            if (tvWidget) {
                tvWidget.remove()
            }
        }
    }, [containerRef])

    return (
        <div ref={containerRef} style={{ height: props.height }} />
    )
}
