import {backendAddress} from "../constants";
import {SwapType} from "../mock/getTableData";
import {Bar} from "@charting-library";

export interface Operation {
    type: 'buy' | 'sell'

    priceUSD: number
    priceETH: number

    amount: number

    totalETH: number
    time: number
    maker: string
}

export interface CurrencyData {
    name: string
    secondToken: string

    address: string

    changePercentage: number
    cost: number

    liquidity: number
    volume: number
    ethPrice: number

    mktCap?: string
    supply?: string

    swap: SwapType

    trade_data: {
        operations: Operation[]
    }
}

interface NotFoundAnswer {
    detail: string
}

export async function fetchCurrency(pairAddress: string): Promise<CurrencyData | NotFoundAnswer> {
    const request = await fetch(`${backendAddress}/currency/fetch/${pairAddress}`)

    // TO DO remove this lines after back-end api update.
    const data = await request.json() as CurrencyData | NotFoundAnswer

    if (data && 'address' in data && !data.secondToken) {
        data.secondToken = 'ETH'
    }

    return data

    // and uncomment this
    // return await request.json()
}

export type WebSocketEvent = { type: 'OPERATIONS', data: Operation[] } | { type: 'CHARTS', data: Bar[] }
