import {backendWSAddress} from "../../../api/constants";
import { SubscribeBarsCallback, ResolutionString } from "@charting-library";
import {WebSocketEvent} from "../../../api/methods/fetchCurrency";
import {store} from "../../../redux/store";
import { openWebsocket, closeWebsocket } from "../../../redux/slices/websocket.slice";
import {resolutionToMinutes} from "./datafeed";

export function subscribe(
    interval: ResolutionString,
    symbolName: string,
    callback: SubscribeBarsCallback
) {
    store.dispatch(openWebsocket(
        `${backendWSAddress}/${symbolName}?resolution=${resolutionToMinutes[interval]}`
    ))

    const ws = store.getState().websocket.value

    if (ws) {
        ws.addEventListener('message', (event) => {
            const data = JSON.parse(event.data) as WebSocketEvent

            if (data.type === 'CHARTS' && data.data[0]) {
                callback(data.data[0])
            }
        })
    }
}

export function unsubscribe() {
    store.dispatch(closeWebsocket())
}

