import React, {useEffect, useState} from 'react'
import styles from './styles.module.css'
import {
    AppBar,
    Button,
    CircularProgress,
    Icon,
    InputAdornment, TextField,
    Toolbar,
    Typography
} from "@mui/material";
import {Search} from "../home/Search";
import {makeStyles} from "@material-ui/styles";
import {CurrenciesLine} from "./DumbComponents/CurrenciesLine";
import {CurrencyInfo} from "./DumbComponents/CurrencyInfo";
import {OperationsTable} from "./DumbComponents/OperationsTable";
import logo from "../../img/logo.png";

import { Link } from 'react-router-dom';
import {CurrencyData, fetchCurrency, Operation, WebSocketEvent} from "../../api/methods/fetchCurrency";
import {getCurrenciesTop, TopCurrency} from "../../api/methods/getCurrenciesTop";
import {Chart} from "../../components/Chart/Chart";
import {useAppSelector} from "../../redux/hooks";

export interface DashboardProps {
    id?: string
}

export const Dashboard = ({ id } : DashboardProps) => {
    const wsConnection = useAppSelector(s => s.websocket.value)

    const [visible, setIsVisible] = useState(false)

    const [query, setQuery] = useState('')
    const muiStyles = useStyles()

    const [topData, setTopData] = useState<TopCurrency[]>([])
    const [data, setData] = useState<CurrencyData>()
    const [operations, setOperations] = useState<Operation[]>([])
    const [lastEvent, setLastEvent] = useState<WebSocketEvent>()

    const [loading, setIsLoading] = useState(true)

    useEffect(() => {
        async function fetchData() {
            const data = await fetchCurrency(id || '')

            if (data && 'trade_data' in data) {
                setData(data)
                setTopData(await getCurrenciesTop())

                setOperations(data.trade_data.operations)
            }

            setIsLoading(false)
        }

        void fetchData()

        return () => {
            if (wsConnection) {
                wsConnection.close()
            }
        }
    }, [])

    useEffect(() => {
        if (wsConnection) {
            wsConnection.addEventListener('message', e => {
                setLastEvent(JSON.parse(e.data))
            })
        }
    }, [wsConnection])

    useEffect(() => {
        if (!lastEvent)
            return

        if (lastEvent.type !== 'CHARTS') {
            const op = lastEvent.data.concat(operations)

            while (op.length > 10) {
                op.pop()
            }

            setOperations(op)
        }
    }, [lastEvent])

    return (
        <div className={styles['page-container']}>
            <AppBar elevation={0} position="fixed">
                <Toolbar>
                    <Link to="/">
                        <img className={styles['logo']} src={logo} alt="logo"/>
                    </Link>

                    <Search
                        query={query}
                        visible={visible}
                        onClose={q => {
                            setIsVisible(false)
                            setQuery(q)
                        }}
                    />

                    <TextField
                        onClick={() => setIsVisible(true)}
                        value={query}
                        className={`${muiStyles.searchField} ${styles['search-field']}`}
                        placeholder="Search by token name, ticker, address or pair"
                        InputProps={{
                            startAdornment: (
                                <InputAdornment position="start">
                                    <Icon style={{ color: 'rgb(115, 104, 231)' }}>
                                        <svg stroke="currentColor" fill="none" strokeWidth="2" viewBox="0 0 24 24" strokeLinecap="round" strokeLinejoin="round" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg"><circle cx="11" cy="11" r="8"/><line x1="21" y1="21" x2="16.65" y2="16.65"/></svg>
                                    </Icon>
                                </InputAdornment>
                            )
                        }}
                    />

                    <Button
                        className={styles['search-button']}
                        onClick={() => setIsVisible(true)}
                        sx={{ borderRadius: '50%', minWidth: 0, height: '40px' }}
                    >
                        <Icon style={{ color: 'rgb(115, 104, 231)', display: 'flex' }}>
                            <svg stroke="currentColor" fill="none" strokeWidth="2" viewBox="0 0 24 24" strokeLinecap="round" strokeLinejoin="round" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg"><circle cx="11" cy="11" r="8"/><line x1="21" y1="21" x2="16.65" y2="16.65"/></svg>
                        </Icon>
                    </Button>
                </Toolbar>
            </AppBar>

            {loading ? (
                // if loading
                <div className={muiStyles.searchOrNotFoundContainer}>
                    <CircularProgress/>
                </div>
            ) : (
                data ? (
                    // if data && !loading
                    <div className={styles['page-content']}>
                        <CurrenciesLine
                            currencies={topData}
                        />

                        <CurrencyInfo data={data} />

                        <div style={{ width: '100%', marginTop: '32px', height: '400px' }}>
                            <Chart
                                symbol={id || ''}
                                height={400}
                            />
                        </div>

                        <OperationsTable operations={operations}/>
                    </div>
                ) : (
                    // if !data && !loading
                    <div className={muiStyles.searchOrNotFoundContainer}>
                        <Typography
                            variant="h3"
                            sx={{
                                fontFamily: 'Poppins',
                                color: 'rgb(137, 255, 211)'
                            }}
                        >
                            Not Found.
                        </Typography>
                    </div>
                )
            )}
        </div>
    )
}

const useStyles = makeStyles({
    searchOrNotFoundContainer: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            height: '100vh',
            flex: '1 1 0%',
    },

    searchField: {
        width: '50%',
        minWidth: '512px',
        margin: '8px !important',

        boxSizing: 'border-box',
        outline: 'none',

        '&>.MuiOutlinedInput-root': {
            padding: '4px 20px',
            borderRadius: '8px',
            backgroundColor: 'rgb(26, 20, 51)',
            color: 'rgb(143, 134, 235)',
            fontFamily: 'Poppins',

            '&:hover': {
                '&>.MuiOutlinedInput-notchedOutline': {
                    borderColor: 'rgba(115, 104, 231, 0.533)',
                    borderWidth: '2px'
                }
            }
        },
    }
})
