import React, {useEffect, useState} from 'react'

import {
    Avatar,
    AvatarGroup, CircularProgress,
    Paper,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableRow, Typography
} from "@mui/material";

import { Box } from "@mui/material";
import {makeStyles} from "@material-ui/styles";
import {CurrenciesTableHead} from "./DumbComponents/TableHead";
import {PercentageChangeCell} from "./DumbComponents/PercentageChangeCell";
import {formatMoney} from "../../utils/formatMoney";
import {FoundedCurrency, searchCurrency} from "../../api/methods/searchCurrency";
import {useHistory} from "react-router";

interface CurrenciesTableProps {
    searchRequest: string
}

export const CurrenciesTable = (props: CurrenciesTableProps) => {
    const [loading, setIsLoading] = useState(true)
    const [currencies, setCurrencies] = useState<FoundedCurrency[]>([])
    const history = useHistory()

    const styles = useStyles()

    useEffect(() => {
        async function search() {
            setCurrencies(await searchCurrency(props.searchRequest))
            setIsLoading(false)
        }

        setIsLoading(true)
        void search()
    }, [props.searchRequest])

    if (loading || !currencies.length) {
        return (
            <div style={{ height: '80px', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                {loading ? (
                    <CircularProgress/>
                ) : (
                    <Typography
                        variant="h6"
                        sx={{
                            fontFamily: 'Poppins',
                            color: 'rgb(143, 134, 235)'
                        }}
                    >
                        Currencies not found.
                    </Typography>
                )}
            </div>
        )
    }

    return (
        <Box sx={{ width: '100%' }} className={styles.box}>
            <Paper sx={{ width: '100%', mb: 2 }} elevation={0}>
                <TableContainer>
                    <Table
                        aria-labelledby="tableTitle"
                        size="medium"
                    >
                        <CurrenciesTableHead
                            cellStyle={styles.cell}
                        />
                        <TableBody>
                            {currencies
                                .map((row, index) => (
                                    <TableRow
                                        hover
                                        className={styles.dataRow}
                                        onClick={() => {
                                            history.push('/' + row.address)
                                        }}
                                    >
                                        <TableCell
                                            component="th"
                                            scope="row"
                                            padding="none"
                                        >
                                            {Array.isArray(row.dex) ? (
                                                <AvatarGroup max={2}>
                                                    {row.dex.map(url => (
                                                        <Avatar className={styles.image} src={url}/>
                                                    ))}
                                                </AvatarGroup>
                                            ) : <Avatar className={styles.image} src={row.dex}/>}
                                        </TableCell>
                                        <TableCell
                                            component="th"
                                            scope="row"
                                            padding="none"
                                        >
                                            <div>
                                                <Avatar className={styles.image} src={row.name.iconUrl}/>
                                                <span>{row.name.title}</span>
                                            </div>
                                        </TableCell>
                                        <TableCell
                                            component="th"
                                            scope="row"
                                            padding="none"
                                        >
                                            <span>${formatMoney(row.volume)}</span>
                                        </TableCell>
                                        <TableCell
                                            component="th"
                                            scope="row"
                                            padding="none"
                                        >
                                            <span>${formatMoney(row.liquidity)}</span>
                                        </TableCell>
                                        <TableCell
                                            component="th"
                                            scope="row"
                                            padding="none"
                                        >
                                            <span>${row.price}</span>
                                        </TableCell>
                                        <TableCell
                                            component="th"
                                            scope="row"
                                            padding="none"
                                        >
                                            <PercentageChangeCell percentage={row.change} />
                                        </TableCell>
                                    </TableRow>
                                ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </Paper>
        </Box>
    )
}

const useStyles = makeStyles({
    box: {
        '&>.MuiPaper-root': {
            backgroundColor: 'transparent',
            border: 'none',
            minWidth: '700px'
        },

        overflow: 'auto'
    },
    cell: {
        fontFamily: 'Poppins !important',
        fontWeight: 400,
        color: 'rgb(115, 104, 231) !important',
        border: 'none !important',

        '&:not(:first-child)': {
            paddingLeft: '0 !important',
            paddingRight: '0 !important'
        },

        '&:first-child': {
            paddingRight: '0 !important'
        }
    },

    image: {
        width: '20px !important',
        height: '20px !important'
    },

    dataRow: {
        '&>.MuiTableCell-root:first-child': {
            paddingLeft: '16px',

            '&>.MuiAvatarGroup-root': {
                marginLeft: '-2px',
                width: 'fit-content',

                '&>div': {
                    border: '2px solid rgb(26, 20, 51)'
                }
            }
        },

        '&>.MuiTableCell-root:nth-child(2)>div': {
            display: 'flex',
            fontFamily: 'Poppins',
            fontWeight: '500',
            flexDirection: 'row',
            alignItems: 'center',

            '&>span': {
                marginLeft: '8px'
            },

            fontSize: '1.3em'
        },

        '&>.MuiTableCell-root': {
            padding: '8px 0',
            border: 'none',

            marginLeft: 'auto',
            marginRight: 'auto',

            color: 'white',
            fontFamily: 'Fira Mono',
        },

        '&:hover': {
            backgroundColor: 'rgba(80, 72, 161, 0.3) !important',
            cursor: 'pointer'
        }
    },
})
