import React from 'react'
import {BrowserRouter, Route, Switch} from "react-router-dom";
import {Home} from "./pages/home/Home";
import {Dashboard} from "./pages/dashboard/Dashboard";

interface DashboardProps {
    match: {
        params: {
            id?: string
        }
    }
}

function App() {
    return (
        <BrowserRouter>
            <Switch>
                <Route path="/" exact>
                    <Home/>
                </Route>

                <Route
                    path="/:id"
                    component={(props : DashboardProps) => <Dashboard id={props.match.params.id}/>}
                />
            </Switch>
        </BrowserRouter>
    )
}

export default App
