import React, { useState } from 'react'
import styles from './home.module.css'
import {AppBar, Icon, InputAdornment, TextField, Toolbar} from "@mui/material"
import { makeStyles } from '@material-ui/styles'

import logo from '../../img/logo.png'
import nsfwLogo from '../../img/nsfw-logo.png'

import {Search} from "./Search";
import {Link} from "react-router-dom"

import twitterLogo from '../../img/twitter-app.svg'
import telegramLogo from '../../img/telegram.svg'
import discordLogo from '../../img/discord.svg'

export const Home = () => {
    const muiStyles = useStyles()

    const [visible, setIsVisible] = useState(false)
    const [query, setQuery] = useState('')

    return (
        <div className={styles['page-container']}>
            <AppBar elevation={0} position="fixed">
                <Toolbar>
                    <Link to="/">
                        <img className={styles['logo']} src={logo} alt="logo"/>
                    </Link>
                </Toolbar>
            </AppBar>
            <div className={styles['header']}>
                <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                    <img src={nsfwLogo} alt="nsfw-logo" className={styles['nsfw-logo']}/>

                    <Search
                        query={query}
                        visible={visible}
                        onClose={q => {
                            setIsVisible(false)
                            setQuery(q)
                        }}
                    />
                </div>

                <TextField
                    onClick={() => setIsVisible(true)}
                    value={query}
                    className={`${styles['search']} ${muiStyles.searchField} ${visible ? styles['visible'] : styles['hidden']}`}
                    placeholder="Search by token name, ticker, address or pair"
                    InputProps={{
                        startAdornment: (
                            <InputAdornment position="start">
                                <Icon style={{ color: 'rgb(115, 104, 231)' }}>
                                    <svg stroke="currentColor" fill="none" strokeWidth="2" viewBox="0 0 24 24" strokeLinecap="round" strokeLinejoin="round" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg"><circle cx="11" cy="11" r="8"/><line x1="21" y1="21" x2="16.65" y2="16.65"/></svg>
                                </Icon>
                            </InputAdornment>
                        )
                    }}
                />
            </div>

            <div className={styles['social-links']}>
                <div>
                    <a target="_blank" rel="noopener noreferrer" href="https://discord.gg/H2qdv35k4k">
                        <img src={discordLogo} alt="discord logo"/>

                        Discord
                    </a>
                    <a target="_blank" rel="noopener noreferrer" href="https://t.me/boobaAnnouncements">
                        <img src={telegramLogo} alt="discord logo"/>

                        Telegram Announcements
                    </a>
                </div>

                <div>
                    <a target="_blank" rel="noopener noreferrer" href="https://t.me/boobaProtocol">
                        <img src={telegramLogo} alt="discord logo"/>

                        Telegram Official Chat
                    </a>
                    <a target="_blank" rel="noopener noreferrer" href="https://twitter.com/Booba_Fi">
                        <img src={twitterLogo} alt="discord logo"/>

                        Twitter
                    </a>
                </div>
            </div>
        </div>
    )
}

const useStyles = makeStyles({
    searchField: {
        width: '90%',
        maxWidth: '700px',
        marginTop: '64px !important',

        boxSizing: 'border-box',
        outline: 'none',

        '&>.MuiOutlinedInput-root': {
            padding: '4px 20px',
            borderRadius: '8px',
            backgroundColor: 'rgb(26, 20, 51)',
            color: 'rgb(143, 134, 235)',
            fontFamily: 'Poppins',

            '&:hover': {
                '&>.MuiOutlinedInput-notchedOutline': {
                    borderColor: 'rgba(115, 104, 231, 0.533)',
                    borderWidth: '2px'
                }
            }
        },
    }
})

