import {TableCell, TableHead, TableRow} from "@mui/material";
import React from "react";

export const CurrenciesTableHead = ({ cellStyle } : { cellStyle: any }) => (
    <TableHead>
        <TableRow>
            <TableCell width="10%" className={cellStyle}>DEXs</TableCell>
            <TableCell width="20%" className={cellStyle}>Name</TableCell>
            <TableCell width="12.5%" className={cellStyle}>Volume</TableCell>
            <TableCell width="12.5%" className={cellStyle}>Liquidity</TableCell>
            <TableCell width="20%" className={cellStyle}>Price</TableCell>
            <TableCell width="10%" className={cellStyle}>Change</TableCell>
        </TableRow>
    </TableHead>
)
