import {createSlice, PayloadAction} from "@reduxjs/toolkit"
import {RootState} from "../store";

// Define a type for the slice state
interface WSState {
    value?: WebSocket
}

// Define the initial state using that type
const initialState: WSState = {
    value: undefined,
}

export const wsSlice = createSlice({
    name: 'websocket',
    initialState,
    reducers: {
        // action payload = websocket address
        openWebsocket: (state, action: PayloadAction<string>) => {
            state.value = new WebSocket(action.payload)
        },

        closeWebsocket(state) {
            state.value?.close()

            state.value = undefined
        }
    },
})

export const { openWebsocket, closeWebsocket } = wsSlice.actions
export const selectWebsocket = (state: RootState) => state.websocket.value

export const wsReducer = wsSlice.reducer
