import {
    PercentageIndicatorDown,
    PercentageIndicatorUp, PercentageIndicatorZero
} from "../../../components/PercentageIndicator/PercentageIndicator";
import React from 'react'

export const PercentageChangeCell = ({ percentage } : { percentage: number }) => {
    const text = Math
        .abs(percentage)
        .toLocaleString(
            'en-US',
            { minimumFractionDigits: 2, maximumFractionDigits: 2 }
        ) + '%'

    if (percentage > 0) {
        return (
            <span style={{ color: 'rgb(0, 195, 147)' }}>
                <PercentageIndicatorUp/>
                {text}
            </span>
        )
    } else if (percentage < 0) {
        return (
            <span style={{ color: 'rgb(239, 66, 91)' }}>
                <PercentageIndicatorDown/>
                {text}
            </span>
        )
    } else {
        return (
            <span style={{ color: 'rgb(107, 124, 118)' }}>
                <PercentageIndicatorZero/>
                {text}
            </span>
        )
    }
}
