import {
    Dialog,
    DialogContent,
    Icon,
    InputAdornment,
    TextField
} from "@mui/material";
import React from "react";
import {makeStyles} from "@material-ui/styles";
import {CurrenciesTable} from "./CurrenciesTable";

interface SearchProps {
    query: string

    visible: boolean
    onClose: (query: string) => void
}

export const Search = (props: SearchProps) => {
    const [query, setQuery] = React.useState(props.query)
    const muiStyles = useStyles()

    return (
        <Dialog
            open={props.visible}
            onClose={() => props.onClose(query)}
            maxWidth="md"
            fullWidth
            classes={{ container: muiStyles.dialog }}
        >
            <DialogContent>
                <TextField
                    value={query}
                    onChange={(e) =>
                        setQuery(e.currentTarget.value)
                    }
                    className={muiStyles.searchField}
                    placeholder="Search by token name, ticker, address or pair"
                    InputProps={{
                        startAdornment: (
                            <InputAdornment position="start">
                                <Icon style={{ color: 'rgb(115, 104, 231)' }}>
                                    <svg stroke="currentColor" fill="none" stroke-width="2" viewBox="0 0 24 24" stroke-linecap="round" stroke-linejoin="round" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg"><circle cx="11" cy="11" r="8"></circle><line x1="21" y1="21" x2="16.65" y2="16.65"></line></svg>
                                </Icon>
                            </InputAdornment>
                        )
                    }}
                />

                <CurrenciesTable searchRequest={query}/>
            </DialogContent>
        </Dialog>
    )
}

const useStyles = makeStyles({
    dialog: {
        alignItems: 'flex-start !important',

        '&>.MuiPaper-root': {
            backgroundColor: 'rgb(26, 20, 51) !important',
            borderRadius: '8px',
            maxWidth: '700px !important',
            marginTop: '8px',

            border: '2px solid rgb(115, 104, 231)',

            '&>.MuiDialogContent-root': {
                padding: 0,

                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',

                '&>.MuiFormControl-root': {
                    marginTop: '0 !important'
                }
            }
        }
    },
    searchField: {
        width: '100%',

        boxSizing: 'border-box',
        outline: 'none',

        '&>.MuiOutlinedInput-root': {
            padding: '4px 20px',
            borderRadius: '8px',
            backgroundColor: 'rgb(26, 20, 51)',
            color: 'rgb(143, 134, 235)',
            fontFamily: 'Poppins',

            '&>.MuiOutlinedInput-notchedOutline': {
                border: 'none',
            }
        },
    },
})
