import {Operation} from "../../../api/mock/getOperations";
import {
    Box,
    Paper,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow
} from "@mui/material";
import React from "react";
import {makeStyles} from "@material-ui/styles";

import cssStyles from './styles/operations-table.module.css'
import moment from "moment-timezone";

interface OperationsTableProps {
    operations: Operation[]
}

export const OperationsTable = (props: OperationsTableProps) => {
    const styles = useStyles()

    return (
        <Box sx={{ width: '100%', overflow: 'auto' }} className={styles.box}>
            <Paper sx={{ width: '100%', mb: 2 }} elevation={0}>
                <TableContainer>
                    <Table
                        aria-labelledby="tableTitle"
                        size="medium"
                        style={{ minWidth: '842px' }}
                    >
                        <TableHead>
                            <TableRow>
                                <TableCell width="14%" className={styles.cell}>Type</TableCell>
                                <TableCell width="14%" className={styles.cell}>Price USD</TableCell>
                                <TableCell width="14%" className={styles.cell}>Price ETH</TableCell>
                                <TableCell width="14%" className={styles.cell}>Amount</TableCell>
                                <TableCell width="14%" className={styles.cell}>Total ETH</TableCell>
                                <TableCell width="14%" className={styles.cell}>Time</TableCell>
                                <TableCell width="14%" className={styles.cell}>Maker</TableCell>
                            </TableRow>
                        </TableHead>

                        <TableBody>
                            {props.operations
                                .map((operation, index) => (
                                    <TableRow
                                        hover
                                        className={styles.dataRow}
                                        style={{
                                            backgroundColor: index % 2 === 0 ? '#141414' : void 0
                                        }}
                                        key={index}
                                    >
                                        <TableCell
                                            component="th"
                                            scope="row"
                                            padding="none"
                                        >
                                            <span className={cssStyles[`label-` + operation.type]}>
                                                {operation.type === 'buy' ? 'Buy' : 'Sell'}
                                            </span>
                                        </TableCell>

                                        <TableCell
                                            component="th"
                                            scope="row"
                                            padding="none"
                                        >
                                            ${operation.priceUSD}
                                        </TableCell>

                                        <TableCell
                                            component="th"
                                            scope="row"
                                            padding="none"
                                        >
                                            {operation.priceETH}
                                        </TableCell>

                                        <TableCell
                                            component="th"
                                            scope="row"
                                            padding="none"
                                        >
                                            {operation.amount}
                                        </TableCell>

                                        <TableCell
                                            component="th"
                                            scope="row"
                                            padding="none"
                                        >
                                            {operation.totalETH}
                                        </TableCell>


                                        <TableCell
                                            component="th"
                                            scope="row"
                                            padding="none"
                                        >
                                            {moment.unix(operation.time).fromNow()}
                                        </TableCell>

                                        <TableCell
                                            component="th"
                                            scope="row"
                                            padding="none"
                                        >
                                            <a
                                                style={{ color: 'rgb(129, 242, 200)', textDecoration: 'none' }}
                                                href={`https://arbiscan.io/address/${operation.maker}`}>
                                                ...{operation.maker
                                                .substring(operation.maker.length - 11)}
                                            </a>
                                        </TableCell>
                                    </TableRow>
                                ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </Paper>
        </Box>
    )
}

const useStyles = makeStyles({
    box: {
        marginTop: '32px',

        '&>.MuiPaper-root': {
            border: 'none',

            backgroundColor: '#191919',
            borderRadius: '8px'
        }
    },
    cell: {
        fontFamily: 'Poppins !important',
        fontWeight: 500,
        color: 'white !important',
        borderBottom: '1px solid #8a8a8a !important',

        fontSize: '1rem !important',

        '&:not(:first-child)': {
            paddingLeft: '0 !important',
            paddingRight: '0 !important'
        },

        '&:first-child': {
            paddingRight: '0 !important'
        }
    },

    image: {
        width: '20px !important',
        height: '20px !important'
    },

    dataRow: {
        '&>.MuiTableCell-root:first-child': {
            paddingLeft: '16px',
        },

        '&>.MuiTableCell-root': {
            padding: '8px 0',
            border: 'none',

            marginLeft: 'auto',
            marginRight: 'auto',

            color: 'white',
            fontFamily: 'Fira Mono',
        },

        '&>.MuiTableCell-root:nth-child(6)': {
            padding: '8px 0',
            border: 'none',

            marginLeft: 'auto',
            marginRight: 'auto',

            color: 'white',
            fontFamily: 'Poppins',
        },

        '&>.MuiTableCell-root:last-child': {
            padding: '8px 0',
            border: 'none',

            marginLeft: 'auto',
            marginRight: 'auto',

            color: 'white',
            fontFamily: 'Poppins',
        },

        '&:hover': {
            backgroundColor: '#262626 !important',
            cursor: 'pointer'
        }
    },
})
