import { IBasicDataFeed, ResolutionString, Bar } from '@charting-library'
import {subscribe, unsubscribe} from './stream';
import {backendAddress} from "../../../api/constants";

const hourInMinutes = 60

export const resolutionToMinutes: Record<string, number> = {
    1: 1,
    5: 5,
    15: 15,
    30 : 30,
    60 : hourInMinutes,
    240 : 4 * hourInMinutes,
    720 : 12 * hourInMinutes,
    '1D' : 24 * hourInMinutes
}

let currentInterval: string = '1D'

export const datafeed: IBasicDataFeed = {
    onReady: callback => {
        setTimeout(() => callback({
            supported_resolutions: [
                '1',
                '5',
                '15',
                '30',
                '1h',
                '4h',
                '12h',
                '1d'
            ] as ResolutionString[]
        }))
    },

    searchSymbols: () => {},
    resolveSymbol: (symbolName, onSymbolResolveCallback) => {
        setTimeout(() => {
            onSymbolResolveCallback({
                name: symbolName,
                description: symbolName,
                ticker: symbolName,
                full_name: '',
                exchange: '',
                listed_exchange: '',
                format: 'price',
                supported_resolutions: [
                    '1',
                    '5',
                    '15',
                    '30',
                    '1h',
                    '4h',
                    '12h',
                    '1d'
                ] as ResolutionString[],
                type: 'crypto',
                session: '24x7',
                minmov: 1,
                pricescale: 100, // 	or 100
                timezone: 'Etc/UTC',
                has_intraday: true,
                has_daily: true,
                has_weekly_and_monthly: true,
                currency_code: symbolName,

                has_no_volume: true,
            })
        })
    },

    getBars: (symbolInfo, interval, periodParams, onHistoryCallback, onErrorCallback) => {
        if (!periodParams.firstDataRequest) {
            return onHistoryCallback([])
        }

        if (interval !== currentInterval) {
            currentInterval = interval
            unsubscribe()
        }

        const getChartInfo = async () => {
            const data = await (await fetch(
                `${backendAddress}/currency/charts/${symbolInfo.name}?resolution=${resolutionToMinutes[interval]}`
            )).json() as Bar[]

            if (data && data.length > 0) {
                return data
            } else {
                return []
            }
        }


        getChartInfo().then((res) => onHistoryCallback(res))
    },

    subscribeBars: (symbolInfo, interval, onRealtimeCallback) => {
        subscribe(interval, symbolInfo.name, cb => onRealtimeCallback(cb))
    },

    unsubscribeBars: () => {},
}
