import React, {useState} from 'react'
import styles from './styles/currency-data.module.css'
import {Alert, Badge, Snackbar, Tooltip, Typography} from "@mui/material";
import {defaultIcon} from "../../../api/mock/getTableData";
import {PercentageChangeCell} from "../../home/DumbComponents/PercentageChangeCell";
import {formatMoney} from "../../../utils/formatMoney";
import {CurrencyData} from "../../../api/methods/fetchCurrency";
import {copyToClipboard} from "../../../utils/clipboard";
import {nameToSwap} from "../../../api/constants";

interface CurrencyInfoProps {
    data: CurrencyData
}

export const CurrencyInfo = (props: CurrencyInfoProps) => {
    const [isError, setIsError] = useState(false)
    const [snackOpen, setSnackOpen] = useState(false)

    async function handleClick() {
        setIsError(!(await copyToClipboard(props.data.address)))
        setSnackOpen(true)
    }

    return (
        <div className={styles['currency-info-container']}>
            <Snackbar open={snackOpen} autoHideDuration={2000} onClose={() => setSnackOpen(false)}>
                <Alert severity={isError ? 'error' : 'success'} sx={{ width: '100%' }}>
                    {isError ?
                        'An unexpected error occurred.' : 'Contract address copied in your clipboard!'}
                </Alert>
            </Snackbar>

            <div className={styles['currency-info']}>
                <Badge
                    badgeContent={<img className={styles['swap-icon']} src={nameToSwap[props.data.swap]} alt=""/>}
                    anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
                >
                    <img className={styles['icon']} alt="" src={defaultIcon} />
                </Badge>

                <div className={styles['currency-data']}>
                    <Typography variant="body1">
                        {props.data.name}/{props.data.secondToken}
                    </Typography>

                    <div>
                        <Typography variant="body1">
                            ${props.data.cost}
                        </Typography>

                        <PercentageChangeCell percentage={props.data.changePercentage}/>
                    </div>
                </div>
            </div>

            <div className={styles['other-info']}>
                <div>
                    <span>Liquidity</span>
                    <p>${formatMoney(props.data.liquidity)}</p>
                </div>
                <div>
                    <span>Volume</span>
                    <p>${formatMoney(props.data.volume)}</p>
                </div>
                <div>
                    <span>Mkt Cap</span>
                    <p>{props.data.mktCap ? formatMoney(props.data.volume) : ''}</p>
                </div>
                <div>
                    <span>Supply</span>
                    <p>{props.data.supply ? formatMoney(props.data.volume) : ''}</p>
                </div>
                <div>
                    <span>ETH Price</span>
                    <p>${formatMoney(props.data.ethPrice)}</p>
                </div>
            </div>

            <div className={styles['buttons-panel']}>
                <div className={styles['trade-button']}>
                    Trade
                </div>
                <Tooltip title={`Copy ${props.data.address}`}>
                    <div className={styles['contract-button']} onClick={() => void handleClick()}>
                        Contract

                        <svg stroke="currentColor" fill="currentColor" strokeWidth="0" viewBox="0 0 24 24" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg"><path d="M20,2H10C8.897,2,8,2.897,8,4v4H4c-1.103,0-2,0.897-2,2v10c0,1.103,0.897,2,2,2h10c1.103,0,2-0.897,2-2v-4h4 c1.103,0,2-0.897,2-2V4C22,2.897,21.103,2,20,2z M4,20V10h10l0.002,10H4z M20,14h-4v-4c0-1.103-0.897-2-2-2h-4V4h10V14z"/><path d="M6 12H12V14H6zM6 16H12V18H6z"/></svg>
                    </div>
                </Tooltip>
            </div>
        </div>
    )
}
