import {backendAddress} from "../constants";

export interface FoundedCurrency {
    dex: string | string[]
    name: {
        iconUrl?: string
        title: string
    }
    volume: number
    liquidity: number
    price: number
    change: number
    address: string
}

export async function searchCurrency(name: string): Promise<FoundedCurrency[]> {
    const request = await fetch(`${backendAddress}/currency/search?query=${name}`)

    return await request.json()
}
