import React from 'react'
import styles from './styles/currencies-line.module.css'
import {Currency} from "../../../api/mock/getLineData";
import {Tooltip} from "@mui/material";

interface CurrenciesLineProps {
    currencies: Currency[]
}

const CurrencyPercentageChangeIcon = ({ percentage } : { percentage: number }) => {
    if (percentage > 0) {
        return (
            <svg stroke="currentColor" fill="currentColor" strokeWidth="0" viewBox="0 0 1024 1024" color="#00c393" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg" style={{ color: 'rgb(0, 195, 147)', verticalAlign: 'middle', }}><path d="M868 545.5L536.1 163a31.96 31.96 0 0 0-48.3 0L156 545.5a7.97 7.97 0 0 0 6 13.2h81c4.6 0 9-2 12.1-5.5L474 300.9V864c0 4.4 3.6 8 8 8h60c4.4 0 8-3.6 8-8V300.9l218.9 252.3c3 3.5 7.4 5.5 12.1 5.5h81c6.8 0 10.5-8 6-13.2z"/></svg>
        )
    } else if (percentage < 0) {
        return (
            <svg stroke="currentColor" fill="currentColor" strokeWidth="0" viewBox="0 0 1024 1024" color="#ef425b" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg" style={{ color: 'rgb(239, 66, 91)', verticalAlign: 'middle' }}><path d="M862 465.3h-81c-4.6 0-9 2-12.1 5.5L550 723.1V160c0-4.4-3.6-8-8-8h-60c-4.4 0-8 3.6-8 8v563.1L255.1 470.8c-3-3.5-7.4-5.5-12.1-5.5h-81c-6.8 0-10.5 8.1-6 13.2L487.9 861a31.96 31.96 0 0 0 48.3 0L868 478.5c4.5-5.2.8-13.2-6-13.2z"/></svg>
        )
    }

    return <></>
}

export const CurrenciesLine = ({ currencies } : CurrenciesLineProps) => (
    <div className={styles['currencies-line']}>
        {currencies.map((c, index) => (
            <Tooltip
                title={`${c.fullName} ${
                    c.changePercentage.toLocaleString(
                        'en-US',
                        { minimumFractionDigits: 2, maximumFractionDigits: 2 }
                    )
                }%`}
                key={index}
            >
                <div className={styles['currency-button']}>
                    <span className={styles['currency-position']}>{index + 1}</span>

                    <CurrencyPercentageChangeIcon percentage={c.changePercentage}/>

                    <span className={styles['currency-name']}>{c.name}</span>
                </div>
            </Tooltip>
        ))}
    </div>
)

